  width: 20px;
  height: 20px;
  top: -36px;
  right: 0px;
  transition: all linear 0.2s;
  border-radius: 3px;
  background: #dddddd;
}
.custom-carousel .owl-controls .owl-next:before {
  content: "\f105";
